@use '../base/variables' as v;
@use '../base/mixins' as m;


.contNavbar{
    width: 100%;
    .navbar{
        width: 100%;
        display: flex;
        background-color: v.$amarilloClaro;
        .navbarElemento{
            text-align: center;
            width: 100%;
            height: 100%;
            border: none;
            padding:  1rem;
            margin: 0;
            
            font-size: 2.3rem;
            color: v.$grisOpaco;
            font-weight: bold;
            &:hover{
                cursor: pointer;
            }
        }
        .seleccionado{
            @extend .navbarElemento;
            background-color: v.$grisoscuro;
            color: v.$grisclaro;
            // text-shadow:  1px 1px 3px v.$amarillo;
        }
    }
}

@include m.desktop{
    .contNavbar{
        position: absolute;
        right: 0;
        top: 0;
        width: 50px;
        .navbar{
            width: 100%;
            height: 100vh;
            transform: rotate(180deg);
            writing-mode: vertical-rl;
            display: flex;
            flex-direction: row-reverse;
            //justify-content: space-evenly;
        }
    }
}