html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background: #fcf4f4;
  font-family: "Roboto", monospace;
  font-size: 1.6rem;
  color: #0A0A0C;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2rem;
  line-height: 1.5;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #e1e1e1;
}
a:hover {
  cursor: pointer;
}

p {
  margin: 1rem 0;
  letter-spacing: 1.5px;
}

.a {
  text-decoration: none;
  color: #e1e1e1;
}
.a:hover {
  cursor: pointer;
}

h1 {
  font-size: 4rem;
  color: #0A0A0C;
}

h2 {
  font-size: 3.6rem;
  color: #0A0A0C;
  margin: 2rem 0;
}

h3 {
  font-size: 2.5rem;
  margin: 0;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.enlaces {
  color: #1b1835;
}

::-webkit-scrollbar {
  display: none;
}

.contenido {
  width: 95%;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .estructura {
    position: relative;
    display: flex;
    max-width: 100vw;
    justify-content: space-evenly;
  }
  .estructura .contenido {
    height: 100vh;
    overflow-y: scroll;
    scrollbar-width: none;
    padding-right: 40px;
    width: 65%;
    margin: 0 auto;
  }
}
.contNavbar {
  width: 100%;
}
.contNavbar .navbar {
  width: 100%;
  display: flex;
  background-color: #362f6d;
}
.contNavbar .navbar .navbarElemento, .contNavbar .navbar .seleccionado {
  text-align: center;
  width: 100%;
  height: 100%;
  border: none;
  padding: 1rem;
  margin: 0;
  font-size: 2.3rem;
  color: #b9b9b9;
  font-weight: bold;
}
.contNavbar .navbar .navbarElemento:hover, .contNavbar .navbar .seleccionado:hover {
  cursor: pointer;
}
.contNavbar .navbar .seleccionado {
  background-color: #1b1835;
  color: #e1e1e1;
}

@media (min-width: 768px) {
  .contNavbar {
    position: absolute;
    right: 0;
    top: 0;
    width: 50px;
  }
  .contNavbar .navbar {
    width: 100%;
    height: 100vh;
    transform: rotate(180deg);
    writing-mode: vertical-rl;
    display: flex;
    flex-direction: row-reverse;
  }
}
.enlace {
  width: 100%;
  margin: 0 auto;
}

@media (min-width: 320px) {
  .contenedor-pages {
    display: block;
    margin: 0 auto;
  }
}
@media (min-width: 768px) {
  .contenedor-pages {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
    align-items: center;
    margin: 0 auto;
  }
  .contenedor-pages .contenedor-pagesElemento {
    border-radius: 0.5rem;
    padding: 1rem;
    margin: 0 auto;
  }
  .contenedor-pages .contenedor-pagesElemento .subtitulo {
    font-size: 1.2rem;
  }
  .contenedor-pages .contenedor-pagesElemento img:hover {
    transition: 0.4s;
    transform: scale(1.05);
  }
  .contenedor-pages .contenedor-pagesElemento:hover {
    background-color: #1b1835;
  }
  .contenedor-pages .contenedor-pagesElemento:hover .subtitulo {
    color: #e1e1e1;
  }
}
.TechnicalSkills {
  width: 95%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 2rem;
  align-items: center;
}
.TechnicalSkills p {
  background-color: #1b1835;
  box-shadow: 3px 6px 14px 0px rgba(58, 58, 58, 0.36);
  color: #e1e1e1;
  font-weight: 700;
  padding: 1rem;
  border-radius: 0.5rem;
  text-align: center;
  margin: 0;
}
.TechnicalSkills .years {
  color: #EEAD35;
  font-size: 1.2rem;
}

.preguntas {
  color: #0A0A0C;
}

.trabajo {
  margin-top: 2rem;
  border-bottom: solid 1px #e1e1e1;
  background-color: #362f6d;
  color: #e1e1e1;
  padding: 0 2rem;
  border-radius: 0.5rem;
}
@media (min-width: 768px) {
  .trabajo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 2rem;
  }
}

.linea-tiemp {
  display: flex;
  gap: 1rem;
  padding: 2rem 0;
}
.linea-tiemp .cont-1 {
  flex: 0 60%;
}
.linea-tiemp .cont-2 {
  flex: 1;
}
.linea-tiemp .fecha-work {
  display: flex;
  gap: 2rem;
  justify-content: space-between;
}
.linea-tiemp .fecha-work small {
  padding: 0;
  margin: 0;
  color: #b9b9b9;
}
.linea-tiemp .work {
  padding: 0 1rem;
  color: #e1e1e1;
  border-radius: 0.5rem;
}
.linea-tiemp .work p {
  margin: 0;
}
.linea-tiemp .free {
  background-color: #1b1835;
}
.linea-tiemp .pxsol {
  background-color: rgb(212, 12, 12);
}

@media (min-width: 320px) {
  .perfil {
    width: 100%;
    height: 100vh;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    background-color: #1b1835;
  }
  .perfil .perfil-texto {
    width: 70%;
    margin: 0 auto;
    text-align: left;
  }
  .perfil .perfil-texto .hola {
    color: #e1e1e1;
  }
  .perfil .perfil-texto .ubi {
    color: #e1e1e1;
  }
  .perfil .nombre {
    font-size: 3.6rem;
    text-transform: uppercase;
  }
  .perfil .carrera {
    font-size: 2.5rem;
    color: #e1e1e1;
  }
  .perfil .ubicacion {
    display: flex;
    align-items: baseline;
    justify-content: left;
    gap: 0.5rem;
  }
  .perfil .iconos {
    width: 60%;
    height: 60px;
    display: flex;
    justify-content: flex-start;
    gap: 2rem;
  }
  .perfil .iconos .icon {
    font-size: 50px;
    color: #e1e1e1;
  }
}
@media (min-width: 768px) {
  .perfil {
    width: 25%;
    margin-right: 1rem;
  }
}
.contFooter {
  border-top: solid 1px #e1e1e1;
  text-align: center;
  margin-top: 2rem;
}
.contFooter p {
  font-size: 1.5rem;
}